
import XButton from 'vux/src/components/x-button/index.vue'


export default {
name: 'GetLocation',
data(){
return {
mapObj: '',
location: ''
}
},
mounted(){
this.mapObj = new AMap.Map('container',{
resizeEnable: true,//是否启用调整
zoom: 10,//变焦
center: [116.3972282409668, 39.90960456049752]
});

this.mapObj.plugin(['AMap.ToolBar','AMap.AdvancedInfoWindow'],() => {

let toolBar = new AMap.ToolBar();
this.mapObj.addControl(toolBar);
})
},
components:{
XButton
},
methods:{
showPlugin(){
let thisObj = this.mapObj;
let that = this;
this.$vux.confirm.show({
title: '允许获取您的地理位置?',
content: '',
onCancel () {
that.$vux.toast.text('您点击了取消，获取地理位置失败')
},
onConfirm () {

thisObj.plugin('AMap.Geolocation', () => {
let geolocation = new AMap.Geolocation({
enableHighAccuracy: true,//是否使用高精度定位，默认:true
timeout: 5000,          //超过10秒后停止定位，默认：无穷大
maximumAge: 0,           //定位结果缓存0毫秒，默认：0
convert: true,           //自动偏移坐标，偏移后的坐标为高德坐标，默认：true
showButton: true,        //显示定位按钮，默认：true
buttonPosition: 'LB',    //定位按钮停靠位置，默认：'LB'，左下角
buttonOffset: new AMap.Pixel(10, 20),//定位按钮与设置的停靠位置的偏移量，默认：Pixel(10, 20)
showMarker: true,        //定位成功后在定位到的位置显示点标记，默认：true
showCircle: true,        //定位成功后用圆圈表示定位精度范围，默认：true
panToLocation: true,     //定位成功后将定位到的位置作为地图中心点，默认：true
zoomToAccuracy:true      
});

thisObj.addControl(geolocation);

geolocation.getCurrentPosition((status,result) => {
that.location = result.formattedAddress;
})
});
}
})
},
}
}



